import { gsap } from 'gsap';

export default function loader() {
	const el = document.querySelector('.loader');
	if (!el) return false;

	const line        = el.querySelector('.loader__line');
	const status      = el.querySelector('.loader__status');
	const pageWrapper = document.querySelector('.page-wrapper');
	const pageContainer = document.querySelector('.page-wrapper > .container--main');
	const heroImage   = document.querySelector('.hero__image');
	gsap.set(
		el,
		{
			clipPath : 'polygon(0px 0%, 100% 0%, 100% 100%, 0px 100%)',
		},
	);
	gsap.set(
		pageWrapper,
		{
			opacity : 0,
		},
	);
	gsap.set(
		pageContainer,
		{
			y : '30px',
		},
	);
	gsap.set(
		heroImage,
		{
			clipPath : 'polygon(0px 0%, 100% 0%, 100% 0%, 0px 0%)',
			opacity  : 0.5,
		},
	);

	function loaderEnd() {
		const tl = gsap.timeline({
			onComplete : () => {
				document.body.classList.remove('body--loader');
			},
		});
		tl
			.to(
				el,
				{
					duration : 0.8,
					ease     : 'sine.out',
					clipPath : 'polygon(0px 0%, 100% 0%, 100% 0%, 0px 0%)',
				},
			)
			.to(
				pageWrapper,
				{
					duration   : 0.8,
					ease       : 'sine.out',
					opacity    : 1,
					clearProps : 'all',
				},
				'-=0.5',
			)
			.to(
				pageContainer,
				{
					y          : '0',
					duration   : 0.8,
					ease       : 'sine.out',
					clearProps : 'all',
				},
				'<',
			)
			.to(
				heroImage,
				{
					duration : 0.8,
					opacity  : 1,
					clipPath : 'polygon(0px 0%, 100% 0%, 100% 100%, 0px 100%)',
					ease     : 'sine.inOut',
				},
				'-=0.9',
			);
	}

	function preload(_success) {
		let width   = 0;
		// eslint-disable-next-line consistent-return
		const timer = setInterval(() => {
			width += 1;
			line.style.width   = `${width}%`;
			status.textContent = `${width}%`;
			if (width === 100) {
				clearInterval(timer);
				width = 0;
				if (_success) {
					return _success();
				}
			}
		}, 20);
	}

	preload(loaderEnd);
}
