import Tabs         from './components/tab';
import accordion    from './components/Accordion';
import burger       from './components/menu';
import smoothScroll from './components/smootScroll';
import MicroModal   from './components/MicroModal';
import scrollPin    from './utils/pinAnimation';
import animationOnScrollFadeInUp, {
	animationOnScrollImage,
}                   from './utils/animationOnScroll';
import magicCursor  from './utils/magicCursor';
import loader       from './utils/loader';

function modal() {
	const settings = {
		openTrigger         : 'data-modal-open',
		closeTrigger        : 'data-modal-close',
		openClass           : 'is-open',
		awaitOpenAnimation  : true,
		awaitCloseAnimation : true,
	};

	MicroModal.init(settings);
	const modalCloseLink = document.querySelector('.js-modal-close');
	if (modalCloseLink) {
		modalCloseLink.addEventListener('click', (e) => {
			e.preventDefault();
			const { id } = e.target.dataset;
			if (id) {
				MicroModal.close(id, settings);
			}
		});
	}
}

// Init
function init() {
	accordion();
	burger();
	smoothScroll();
	scrollPin();
	modal();

	const tabContainers = document.querySelectorAll('.tabs');
	tabContainers.forEach((tabContainer) => new Tabs(tabContainer));

	document.addEventListener('wpcf7mailsent', (e) => {
		window.location.href = '/successfully-submitted';
	}, false);

	// MagicCursor
	magicCursor({
		cursor              : '[data-magic-cursor="main"]',
		inner               : '[data-magic-cursor="inner"]',
		magnetic            : '[data-magic-cursor="magnetic"]',
		magneticAlt         : '[data-magic-cursor="magnetic-alt"]',
		magneticView        : '[data-magic-cursor="view"]',
		magneticClose       : '[data-magic-cursor="close"]',
		magneticInteractive : '[data-magic-cursor="interactive"]',
		breakpoint          : '1081px',
	});

	// Animation on scroll
	const scrollAnimationFade = document.querySelectorAll('[data-animation-on-scroll="fade"]');
	scrollAnimationFade.forEach((item) => animationOnScrollFadeInUp(item));

	const scrollAnimationZoom = document.querySelectorAll('[data-animation-on-scroll="image"]');
	scrollAnimationZoom.forEach((item) => animationOnScrollImage(item));

	loader();
}

(function () {
	init();
}());
