class Tabs {
	constructor(tabContainer) {
		this.tabContainer = tabContainer;
		this.tabLinks     = this.tabContainer.querySelectorAll('.tabs__button');
		this.tabContent   = this.tabContainer.querySelectorAll('.tabs__content');
		this.optionMenu   = this.tabContainer.querySelector('.tabs__head');
		this.selectBtn    = this.optionMenu.querySelector('.tabs__text');
		this.options      = this.optionMenu.querySelectorAll('.tabs__item');
		this.selected     = this.optionMenu.querySelector('.tabs__selected');

		this.tabLinks.forEach((el) => {
			el.addEventListener('click', (e) => this.openTabs(e));
		});

		if (this.selectBtn) {
			this.selectBtn.addEventListener('click', () => this.optionMenu.classList.toggle('active'));
			this.options.forEach((option) => {
				option.addEventListener('click', () => {
					const selectedOption    = option.querySelector('.tabs__button').innerHTML;
					this.selected.innerHTML = selectedOption;
					this.optionMenu.classList.remove('active');
				});
			});
		}
	}

	openTabs(el) {
		const btnTarget = el.currentTarget;
		const { item }    = btnTarget.dataset;

		this.tabContent.forEach((el) => {
			el.classList.remove('active');
		});

		this.tabLinks.forEach((el) => {
			el.classList.remove('active');
		});

		this.tabContainer.querySelector(`#${item}`).classList.add('active');
		btnTarget.classList.add('active');
	}
}

export default Tabs;
