import { gsap }   from 'gsap';
import isMobile from './isMobile';

export default function magicCursor({
	cursor = '[data-magic-cursor="main"]',
	inner = '[data-magic-cursor="inner"]',
	magnetic = '[data-magic-cursor="magnetic"]',
	magneticAlt = '[data-magic-cursor="magnetic-alt"]',
	magneticView = '[data-magic-cursor="view"]',
	magneticClose = '[data-magic-cursor="close"]',
	magneticInteractive = '[data-magic-cursor="interactive"]',
	breakpoint = '1081px',
}) {
	if (isMobile()) return;
	if (!document.querySelector(cursor)) return;
	const mediaQuery = window.matchMedia(`(min-width: ${breakpoint})`);
	if (!mediaQuery.matches) return;
	
	function wrap(toWrap) {
		const newWrapper = document.createElement('div');
		newWrapper.classList.add('magnetic-wrap');
		toWrap.parentNode.appendChild(newWrapper);
		return newWrapper.appendChild(toWrap);
	}
	
	const magneticItems = document.querySelectorAll(magnetic);
	magneticItems.forEach((item) => {
		wrap(item);
		if (item.tagName === 'A') item.classList.add('js-not-hide-cursor');
	});
	
	const $mouse = {
		x : 0,
		y : 0,
	};
	
	// Cursor position
	const $pos = {
		x : 0,
		y : 0,
	};
	
	const $ratio = 0.1; // delay follow cursor
	let $active  = false;
	const $inner = document.querySelector(inner);
	
	const $innerWidth       = 10; // Ball default width
	const $innerHeight      = 10; // Ball default height
	const $innerScale       = 1; // Ball default scale
	const $innerOpacity     = 0.8; // Ball default opacity
	const $innerBorderWidth = 0; // Ball default border width
	
	function mouseMove(e) {
		$mouse.x = e.clientX + 3;
		$mouse.y = e.clientY - 10;
	}
	
	function updatePosition() {
		if (!$active) {
			$pos.x += ($mouse.x - $pos.x) * $ratio;
			$pos.y += ($mouse.y - $pos.y) * $ratio;
			
			gsap.set($inner, {
				x : $pos.x,
				y : $pos.y,
			});
		}
	}
	
	gsap.set($inner, {  // scale from middle and style ball
		xPercent    : -50,
		yPercent    : -50,
		width       : $innerWidth,
		height      : $innerHeight,
		borderWidth : $innerBorderWidth,
		opacity     : $innerOpacity,
	});
	
	document.addEventListener('mousemove', mouseMove);
	
	gsap.ticker.add(updatePosition);
	
	function parallaxIt(e, parent, target, movement) {
		const boundingRect = parent.getBoundingClientRect();
		const relX         = e.clientX - boundingRect.left;
		const relY         = e.clientY - boundingRect.top;
		
		gsap.to(target, {
			duration : 0.3,
			x        : ((relX - boundingRect.width / 2) / boundingRect.width) * movement,
			y        : ((relY - boundingRect.height / 2) / boundingRect.height) * movement,
			ease     : 'power2.out',
		});
	}
	
	function callParallax(e, parent) {
		parallaxIt(e, parent, parent.querySelector(magnetic), 25); // magnetic area = higher number is more attractive
	}
	
	function parallaxCursor(e, parent, movement) {
		const rect = parent.getBoundingClientRect();
		const relX = e.clientX - rect.left;
		const relY = e.clientY - rect.top;
		$pos.x     = rect.left + rect.width / 2 + (relX - rect.width / 2) / movement;
		$pos.y     = rect.top + rect.height / 2 + (relY - rect.height / 2) / movement;
		gsap.to($inner, {
			duration : 0.3,
			x        : $pos.x,
			y        : $pos.y,
		});
	}
	
	function cursorStyle(item) {
		item.addEventListener('mousemove', function (e) {
			parallaxCursor(e, this, 2); // magnetic ball = low number is more attractive
			callParallax(e, this);
		});
		
		// Magnetic item hover.
		item.addEventListener('mouseenter', () => {
			gsap.to($inner, {
				duration    : 0.3,
				scale       : 2,
				borderWidth : 1,
				opacity     : $innerOpacity,
			});
			$active = true;
		});
		
		item.addEventListener('mouseleave', () => {
			gsap.to($inner, {
				duration    : 0.3,
				scale       : $innerScale,
				borderWidth : $innerBorderWidth,
				opacity     : $innerOpacity,
			});
			gsap.to(item.querySelector(magnetic), {
				duration   : 0.3,
				x          : 0,
				y          : 0,
				clearProps : 'all',
			});
			$active = false;
		});
	}
	
	const itemWrapper = document.querySelectorAll('.magnetic-wrap');
	itemWrapper.forEach((wrapper) => cursorStyle(wrapper));
	
	// Magnetic item hover.
	// Alternative cursor style on hover.
	function alternativeCursorStyle(item) {
		item.addEventListener('mouseenter', () => {
			gsap.to($inner, {
				duration        : 0.3,
				borderWidth     : 0,
				opacity         : 0.2,
				backgroundColor : '#CCC',
				width           : '100px',
				height          : '100px',
			});
		});
		
		item.addEventListener('mouseleave', () => {
			gsap.to($inner, {
				duration        : 0.3,
				borderWidth     : $innerBorderWidth,
				opacity         : $innerOpacity,
				backgroundColor : 'transparent',
				width           : $innerWidth,
				height          : $innerHeight,
				clearProps      : 'backgroundColor',
			});
		});
	}
	
	const itemWrapperAlt = document.querySelectorAll(magneticAlt);
	itemWrapperAlt.forEach((item) => alternativeCursorStyle(item));
	
	// Cursor view on hover with text
	function cursorView(item) {
		const { text } = item.dataset;
		if (!text) return;
		
		function mouseViewEnter() {
			const view = document.createElement('div');
			view.classList.add('magic-cursor__view');
			view.innerHTML = text;
			$inner.append(view);
			
			gsap.to($inner, {
				duration        : 0.3,
				yPercent        : -75,
				width           : 95,
				height          : 95,
				opacity         : 1,
				borderWidth     : 0,
				backgroundColor : '#FFF',
			});
			gsap.to(view, {
				duration  : 0.3,
				scale     : 1,
				autoAlpha : 1,
			});
		}
		
		function mouseViewLeave() {
			gsap.to($inner, {
				duration        : 0.3,
				yPercent        : -50,
				width           : $innerWidth,
				height          : $innerHeight,
				opacity         : $innerOpacity,
				borderWidth     : $innerBorderWidth,
				backgroundColor : 'transparent',
			});
			gsap.to('.magic-cursor__view', {
				duration   : 0.3,
				scale      : 0,
				autoAlpha  : 0,
				clearProps : 'all',
			});
			$inner.querySelector('.magic-cursor__view').remove();
		}
		
		item.addEventListener('mouseenter', mouseViewEnter);
		
		item.addEventListener('mouseleave', mouseViewLeave);
		
		item.classList.add('js-not-hide-cursor');
	}
	
	const itemWrapperView = document.querySelectorAll(magneticView);
	itemWrapperView.forEach((item) => cursorView(item));
	
	// Cursor close on hover.
	function closeCursor(item) {
		const { text } = item.dataset;
		
		function closeMouseEnter() {
			const div = document.createElement('div');
			div.classList.add('magic-cursor__close');
			div.textContent = text;
			
			$inner.classList.add('is-close-enabled');
			$inner.append(div);
			
			gsap.to($inner, {
				duration : 0.3,
				yPercent : -75,
				width    : 80,
				height   : 80,
				opacity  : 1,
			});
			gsap.from('.magic-cursor__close', {
				duration  : 0.3,
				scale     : 0,
				autoAlpha : 0,
			});
		}
		
		function closeMouseRemove() {
			$inner.classList.remove('is-close-enabled');
			const closeEl = document.querySelector('.magic-cursor__close');
			gsap.to($inner, {
				duration : 0.3,
				yPercent : -50,
				width    : $innerWidth,
				height   : $innerHeight,
				opacity  : $innerOpacity,
			});
			if (closeEl) {
				closeEl.remove();
			}
		}
		
		item.addEventListener('mouseenter', closeMouseEnter);
		item.addEventListener('mouseleave', closeMouseRemove);
		item.addEventListener('click', closeMouseRemove);
		
		// Hover on "cursor-close" inner elements.
		function removeCloseClass() {
			$inner.classList.remove('is-close-enabled');
		}
		
		function addCloseClass() {
			$inner.classList.add('is-close-enabled');
		}
		
		const innerElements = item.querySelectorAll('a, button, .js-hide-cursor, iframe');
		innerElements.forEach((innerEl) => {
			if (innerEl.classList.contains('js-not-hide-cursor')) return;
			
			innerEl.addEventListener('mouseenter', removeCloseClass);
			innerEl.addEventListener('mouseleave', addCloseClass);
		});
	}
	
	const itemCloseCursor = document.querySelectorAll(magneticClose);
	itemCloseCursor.forEach((item) => closeCursor(item));
	
	// Portfolio interactive title link hover.
	function interactiveCursor(item) {
		const image = item.querySelector('[data-magic-cursor="image"]');
		if (!image) return false;
		
		const title = item.querySelector('[data-magic-cursor="title"]');
		if (!title) return false;
		
		const video = image.querySelector('video');
		
		function mouseInteractiveEnter() {
			document.querySelector(cursor).classList.add('is-interactive-hover-on');
			$inner.append(image);
			gsap.to($inner, {
				duration : 0.3,
				width    : '20vw',
				height   : '20vw',
				opacity  : 1,
			});
			if (video) {
				video.play();
			}
		}
		
		function mouseInteractiveLeave() {
			document.querySelector(cursor).classList.remove('is-interactive-hover-on');
			item.prepend(image);
			gsap.to($inner, {
				duration : 0.3,
				width    : $innerWidth,
				height   : $innerHeight,
				opacity  : $innerOpacity,
			});
			if (video) {
				video.pause();
			}
		}
		
		title.addEventListener('mouseenter', mouseInteractiveEnter);
		title.addEventListener('mouseover', mouseInteractiveEnter);
		title.addEventListener('mouseleave', mouseInteractiveLeave);
		
		title.classList.add('js-not-hide-cursor');
		return false;
	}
	
	const itemInteractiveCursor = document.querySelectorAll(magneticInteractive);
	itemInteractiveCursor.forEach((item) => interactiveCursor(item));
	
	// Page nav hover.
	// $('.tt-page-nav').each(function () {
	// 	if ($(this).find('.tt-pn-image').length) {
	// 		$(this).find('.tt-pn-link').on('mouseenter mouseover', function () {
	// 			$('#magic-cursor').addClass('tt-pn-hover-on');
	// 			$(this).parent().find('.tt-pn-image').appendTo($inner);
	// 			gsap.to(
	// 				$inner,
	// 				{
	// 					duration : 0.3,
	// 					width    : '20vw',
	// 					height   : '20vw',
	// 					opacity  : 1,
	// 				},
	// 			);
	// 			$inner.find('.tt-pn-image video').each(function () {
	// 				$(this).get(0).play();
	// 			});
	// 		}).on('mouseleave', function () {
	// 			$('#magic-cursor').removeClass('tt-pn-hover-on');
	// 			$inner.find('.tt-pn-image').appendTo(this);
	// 			gsap.to(
	// 				$inner,
	// 				{
	// 					duration : 0.3,
	// 					width    : $innerWidth,
	// 					height   : $innerHeight,
	// 					opacity  : $innerOpacity,
	// 				},
	// 			);
	//
	// 			$(this).parent().find('.tt-pn-image video').each(function () {
	// 				$(this).get(0).pause();
	// 			});
	// 		});
	// 		$(this).find('.tt-pn-link').addClass('not-hide-cursor');
	// 	} else {
	// 		$(this).find('.tt-pn-link').removeClass('not-hide-cursor');
	// 	}
	// });
	
	// Show/hide magic cursor
	// =======================
	
	// Hide on hover.
	function hideCursor(item) {
		function cursorHideEnter() {
			gsap.to($inner, {
				duration : 0.3,
				scale    : 0,
				opacity  : 0,
			});
		}
		
		function cursorHideLeave() {
			gsap.to($inner, {
				duration : 0.3,
				scale    : $innerScale,
				opacity  : $innerOpacity,
			});
		}
		
		item.addEventListener('mouseenter', cursorHideEnter);
		item.addEventListener('mouseleave', cursorHideLeave);
	}
	
	const staticEl = document.querySelectorAll('a:not(.js-not-hide-cursor):not([data-magic-cursor="magnetic-alt"]),'
		+ ' button:not(.js-not-hide-cursor):not([data-magic-cursor="magnetic-alt"]), .js-hide-cursor, swiper-button, swiper-slide,'
		+ ' swiper, input, textarea,'
		+ ' iframe');
	staticEl.forEach((item) => hideCursor(item));
	
	// Hide on click.
	function hideCursorOnClick() {
		gsap.to($inner, {
			duration  : 0.3,
			scale     : 1.3,
			autoAlpha : 0,
		});
	}
	
	const staticLink = document.querySelectorAll(
		'a:not([target="_blank"]):not([href^="#"]):not([href^="mailto"]):not([href^="tel"]):not(.is-disabled)',
	);
	staticLink.forEach((item) => item.addEventListener('click', hideCursorOnClick));
	
	// Show/hide on document leave/enter.
	function documentLeave() {
		gsap.to(cursor, {
			duration  : 0.3,
			autoAlpha : 0,
		});
	}
	
	function documentEnter() {
		gsap.to(cursor, {
			duration  : 0.3,
			autoAlpha : 1,
		});
	}
	
	document.addEventListener('mouseleave', documentLeave);
	document.addEventListener('mouseenter', documentEnter);
	document.addEventListener('mousemove', documentEnter);
}
