import { gsap }          from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

gsap.registerPlugin(ScrollTrigger);

const scrollPin = () => {
	const animation  = gsap.timeline();
	const btns       = document.querySelectorAll('.tabs__button--services');
	let cardHeight;
	const breakpoint = window.matchMedia('(min-width : 1439px)');
	
	function initCards() {
		animation.clear();
		const activeContent = document.querySelector('.tabs__content.active');
		if (activeContent) {
			const cards = activeContent.querySelectorAll('.services-item');
			if (cards.length === 1) {
				ScrollTrigger.getAll().forEach((st) => st.disable());
			}
			if (cards.length > 1) {
				if (breakpoint.matches === true) {
					cardHeight = cards[0].offsetHeight + 28;
					cards.forEach((card, index) => {
						if (index > 0) {
							gsap.set(card, { y : index * cardHeight });
							animation.to(
								card,
								{
									y        : 0,
									duration : index * 20,
									ease     : 'none',
								},
								0,
							);
						}
					});
					ScrollTrigger.getAll().forEach((st) => st.enable());
				} else if (breakpoint.matches === false) {
					cards.forEach((card, index) => {
						if (index > 0) {
							gsap.set(card, { y : 0 });
						}
					});
				}
			}
		}
	}
	
	const breakpointChecker = () => {
		if (breakpoint.matches === true) {
			initCards();
			ScrollTrigger.create({
				trigger             : '.tabs--services',
				start               : () => 'top top',
				end                 : () => `+=${(cardHeight) + 700}`,
				pin                 : true,
				pinSpacing          : true,
				scrub               : true,
				animation,
				markers             : false,
				invalidateOnRefresh : true,
			});
			ScrollTrigger.addEventListener('refreshInit', initCards);
			btns.forEach((item) => {
				item.addEventListener('click', () => {
					ScrollTrigger.refresh('refreshInit');
					const { offsetTop } = document.querySelector('.services');
					window.scrollTo({
						top      : offsetTop - 100,
						behavior : 'smooth',
					});
				});
			});
		} else if (breakpoint.matches === false) {
			ScrollTrigger.getAll().forEach((st) => st.kill());
		}
	};
	
	try {
		breakpoint.addEventListener('change', breakpointChecker);
	} catch (e) {
		// Safari <14
		breakpoint.addListener(breakpointChecker);
	}
	
	breakpointChecker();
};

export default scrollPin;
